  display: flex;
  flex-direction: column;
  justify-content: flex-start;
}


.about-me-content {
  margin-right: 10%;
  display: flex;
  flex-wrap: nowrap;
  grid-gap: 1rem;
  gap: 1rem;
  text-align: center;
  justify-content: center;
  align-items: flex-start;
}

.portrait {
  max-width: 200px;
  border-radius: 50%;
