  height: 0;
}

.space {
  height: 60px;
  margin-top: -60px;
}

#about-me,
#contact,
#projects {
  margin-top: -35px;
  padding-top: 65px; /* or however much padding you want to add to make up for the negative margin */
}


.contact-content {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.resume-download {
  margin-top: 1rem;
  font-size: 1.2rem;
  padding: 0.5rem 1rem;
}

.project {
  padding: 1rem;
}

.video-container {
  position: relative;
  width: 80%;
  padding-bottom: 45%; /* 16:9 aspect ratio */
  margin: 1rem auto;
  overflow: hidden;
}

.video-container iframe {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}

nav .nav-link {
  font-size: 1.2rem;
  color: #1d1f2f;
}

nav .nav-link:hover {
  color: #c4a7ff;
}

@media (max-width: 768px) {
  .about-me-content {
    flex-direction: column;
  }

  .about-me-links {
