.header-container {
  font-family: "Helvetica Neue", Arial, sans-serif;
  font-size: 16px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  height: 60px;
  background-color: #333333;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  z-index: 1;
  margin-top: 0;
  -webkit-animation: fadeInDown 1s ease-in-out;
          animation: fadeInDown 1s ease-in-out;
}

@-webkit-keyframes fadeInDown {
  0% {
    -webkit-transform: translateY(-100%);
            transform: translateY(-100%);
    opacity: 0;
  }
  100% {
    -webkit-transform: translateY(0);
            transform: translateY(0);
    opacity: 1;
  }
}

@keyframes fadeInDown {
  0% {
    -webkit-transform: translateY(-100%);
            transform: translateY(-100%);
    opacity: 0;
  }
  100% {
    -webkit-transform: translateY(0);
            transform: translateY(0);
    opacity: 1;
  }
}

.header-text {
  font-size:px;
  color: #ffffff;
  margin-bottom: .1rem;

  text-shadow: 2px 2px #333333;
}

.header-nav {
  display: flex;
  grid-gap: 2rem;
  gap: 2rem;
  justify-content: center;
  align-items: center;
  width: 100%;
  margin-bottom: auto;
  background-color: #444444;
}

.header-nav a {
  font-size: 1.3rem;
  color: #ffffff;
  text-decoration: none;
  margin-top: 0;
  padding: 0.5rem 1.5rem;
}

.header-nav a:hover {
  background-color: #555555;
}

form {
  display: flex;
  flex-direction: column;
  width: 80%;
  margin: 0 auto;
  padding: 2rem;
  border: 2px solid #ccc;
  border-radius: 10px;
}

label {
  margin: 1rem 0;
  font-size: 1.2rem;
}

input,
textarea {
  width: 100%;
  padding: 0.5rem;
  margin-bottom: 1rem;
  border-radius: 5px;
  border: 1px solid #ccc;
  font-size: 1rem;
  resize: vertical;
}

textarea {
  height: 10rem;
}

button[type="submit"] {
  background-color: #1e90ff;
  color: #fff;
  border: none;
  border-radius: 5px;
  padding: 0.5rem 1rem;
  font-size: 1rem;
  cursor: pointer;
  transition: background-color 0.3s ease-in-out;
}

button[type="submit"]:hover {
  background-color: #0077b6;
}

/* Responsive design */
@media (max-width: 768px) {
  .about-me-content {
    flex-direction: column;
  }

  .portrait {
    margin-right: 0;
    margin-bottom: 1rem;
  }
}

@media (min-width: 769px) and (max-width: 1024px) {
  h2 {
    font-size: 2.5rem;
  }

  .header-text {
    font-size: 28px;
  }

  nav .nav-link {
    font-size: 1rem;
  }
}

@media (max-width: 480px) {
  h2 {
    font-size: 2rem;
  }

  .header-text {
    font-size: 24px;
  }

  nav .nav-link {
    font-size: 0.9rem;
  }
}

/* Resume.css */
.resume-container {
    display: flex;
    flex-direction: row;
    align-items: center;
    grid-gap: 1rem;
    gap: 1rem;
    width: 100%;
    margin-top: 10%;
  }
  
  .resume-button {
    font-size: 1.2rem;
    font-family: 'Roboto', sans-serif;
    color: #1d1f2f;
    background-color: #b1a5ca;
    border: none;
    padding: 12px 20px;
    cursor: pointer;
    outline: none;
    border-radius: 5px;
    box-shadow: 0 2px 5px rgba(0, 0, 0, 0.2);
    transition: background-color 0.3s, color 0.3s;
    margin-right: 0.5rem;
  }
  
  .resume-button:hover {
    background-color: #3a3d58;
    color: #847a99;
  }
  
  .resume-preview-container {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 100%;
  }
  
  .resume-preview {
    position: relative;
    width: 170px;
    height: 200px;
    margin-top: 1rem;
    overflow: hidden;
    box-shadow: 0 2px 5px rgba(29, 9, 69, 0.2);
    border-radius: 5px;
  }
  
  .resume-preview iframe {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    border: none;
  }
.typing-text-container {
    position: relative;
    background-color: white;
    border: 1px solid #1d1f2f;
    border-radius: 5px;
    padding: 1rem;
    font-size: 1.2rem;
    font-weight: bold;
    color: #1d1f2f;
    line-height: 2rem;
    width: 50vw;
    height: 30vh;
    margin: 0 auto;
    overflow-wrap: break-word;

  }
  
  .typing-text-container:before {
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-image: linear-gradient(0deg, #1d1f2f 1px, transparent 1px);
    background-size: 100% 2.5rem;
    opacity: 0.2;
    pointer-events: none;
  }
  
  .typing-text {
    position: relative;
  }
  
  .typing-text-cursor {
    font-size: 1.1rem;
    font-weight: bold;
    color: #c4a7ff;
    -webkit-animation: blink 1s infinite;
            animation: blink 1s infinite;
  }
  
  @-webkit-keyframes blink {
    50% {
      opacity: 0;
    }
  }
  
  @keyframes blink {
    50% {
      opacity: 0;
    }
  }
  
.minesweeper-escape {
    font-family: 'Courier New', Courier, monospace;
    text-align: center;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    height: 100vh;

  }
  
  .game-info {
    display: flex;
    justify-content: space-around;
    margin-bottom: 1rem;
  }
  
  button {
    background-color: #4caf50;
    border: none;
    color: white;
    text-align: center;
    text-decoration: none;
    display: inline-block;
    font-size: 16px;
    margin: 4px 2px;
    cursor: pointer;
    border-radius: 5px;
  }
  
  .grid-container {
    display: flex;
    flex-direction: column;
    justify-content: center;
    background-color: black;
    border: 2px solid gray;
    border-radius: 5px;
    padding: 10px;
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
    -webkit-animation: enlarge 1s;
            animation: enlarge 1s;
    -webkit-transform-origin: center center;
            transform-origin: center center;
  }
  
  .grid-row {
    display: flex;
  }
  
  
  .grid-cell {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 30px;
    height: 30px;
    background-color: #212121;
    border: 1px solid #424242;
    color: #ffffff;
    font-family: 'Courier New', Courier, monospace;
    font-size: 20px;
    text-align: center;
  }
  
  .game-info-row {
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    margin-bottom: 1rem;
  }

  @-webkit-keyframes fadeIn {
    from {
      opacity: 0;
    }
    to {
      opacity: 1;
    }
  }

  @keyframes fadeIn {
    from {
      opacity: 0;
    }
    to {
      opacity: 1;
    }
  }
  
  .minesweeper-escape h2,
  .minesweeper-escape button {
    -webkit-animation: fadeIn 1s;
            animation: fadeIn 1s;
  }
  
  @-webkit-keyframes enlarge {
    from {
      -webkit-transform: scale(1);
              transform: scale(1);
    }
    to {
      -webkit-transform: scale(1.5);
              transform: scale(1.5);
    }
  }
  
  @keyframes enlarge {
    from {
      -webkit-transform: scale(1);
              transform: scale(1);
    }
    to {
      -webkit-transform: scale(1.5);
              transform: scale(1.5);
    }
  }
  
